export default function onChangeId(
  e: React.ChangeEvent<HTMLInputElement>,
  setMyId: any,
  setIdMessage: Function,
  setisMyId: Function
) {
  const currentId = e.target.value;
  setMyId(currentId);
  const idRegExp = /^[a-zA-z0-9]{4,12}$/;
  if (!idRegExp.test(currentId)) {
    setIdMessage("4-12     !");
    setisMyId(false);
  } else {
    setIdMessage("   .( ?)");
  }
}
