export default function onChangeName(
  e: React.ChangeEvent<HTMLInputElement>,
  setMyName: Function,
  setNameMessage: Function,
  setisMyName: Function
) {
  const currentName = e.target.value;
  setMyName(currentName);
  const regex = /^[-|-]{2,5}$/;
  if (!regex.test(currentName)) {
    setNameMessage(" 2  5  !");
    setisMyName(false);
  } else {
    setNameMessage("  .");
    setisMyName(true);
  }
}
